@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
}
::-webkit-scrollbar {
  /* chrome based */
  width: 0px; /* ширина scrollbar'a */
  background: transparent; /* опционально */
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  height: 100vh;
  font-family: 'Poppins';
}



.LeftSide{
    display: flex;
    flex-direction: column;
    width: 35%;
    border-left: 1px solid #e2e2e2;
}
.User {
  height: 25%;
  display: flex;
  flex-direction: column;
  background-color: #f5f5f5;
  border-bottom: 1px solid #e2e2e2;
  position: relative;
}
.user-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 50px 10px;
}
.user-img {
  width: 8vh;
  border-radius: 30px;
}
.user-info {
  position: relative;
  display: flex;
  flex-direction: row;
}
.User input {
  margin: 0 auto;
  font-family: FontAwesome;
  width: 95%;
  border-radius: 3vh;
  height: 3vh;
}
.User .far {
  position: absolute;
  top: 6vh;
  left: 6vh;
  color: #719b75;
}

.MemberItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-top: 1px solid #e2e2e2;
  border-bottom: 1px solid #e2e2e2;
}
.member-info {
  margin: 15px;
  display: flex;
  align-items: center;
}
.member-info .member-text {
  display: flex;
  margin-left: 20px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
.member-date {
  margin-right: 20px;
}
.MemberItem img {
  width: 10vh;
  height: 10vh;
  border-radius: 50%;
}
.member-icon {
  position: relative;
}
.member-icon .far {
  position: absolute;
  top: 8vh;
  left: 8vh;
  color: #719b75;
}

.MemberList {
  height: 75vh;
  overflow: scroll;
}
.MemberList h1 {
  color: #3c92b5;
  display: flex;
  font-weight: 300;
  align-items: flex-start;
}

.MessageHeader {
  display: flex;
  height: 13%;
  justify-content: start;
  align-items: center;
  background-color: #f5f5f5;
  border: 1px solid #e8e8e8;
}
.MessageHeader p {
  font-size: 5vh;
  font-weight: bold;
}
.MessageHeader img {
  margin: 10px;
  width: 10vh;
  height: 10vh;
  border-radius: 50%;
}

.MessageField {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  height: 70%;
  overflow: scroll;
}

.Message {
  display: flex;
  margin: 10px;
}
.my-message {
  display: flex;
  align-self: flex-end;
}
.my-message .message-text {
  border-radius: 2vh;
  background-color: #e0e0e0;
  color: #727272;
  padding: 1vh 2vh;
}
.my-message .message-date{
  align-self: flex-end;
}
.message-text {
  border-radius: 2vh;
  background-color: #3e4154;
  color: #eef0f7;
  padding: 1vh 2vh;
}

.message-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.message-icon img {
  width: 5vh;
  height: 5vh;
  border-radius: 50%;
  margin-right: 10px;
}
.message-date {
  margin-left: 10px;
  color: #979797;
  font-size: 14px;
}

.RightSide{
    display: flex;
    flex-direction: column;
    width: 65%;
    border-left: 1px solid #e2e2e2;
}
.MessageSend {
  display: flex;
  position: relative;
  height: 17%;
  width: 100%;
  border-top: 1px solid #e2e2e2;
}
.MessageSend input {
  border: 0px;

  height: 100%;
  width: 100%;
  font-size: 2vh;
  padding: 20px;
}
.MessageSend button {
  cursor: pointer;
  width: 10vh;
  border: 0px;
}
.MessageSend button .far {
  color: #000;
}

